.page {
  display: grid;
  min-height: 100%;
  grid-template-columns: repeat(auto-fit, minmax(320px, 100%));
  grid-template-rows: min-content 1fr min-content; }

.page__header {
  width: 100%; }

.page__main {
  width: 100%;
  display: flex;
  flex-direction: column;
  background-image: url("../img/bg-group-tabs.svg");
  background-repeat: no-repeat;
  background-position: center 80.7%;
  background-size: 75.2%;
  margin-bottom: 118px; }
  @media (max-width: 1500px) {
    .page__main {
      background-position: center 78%;
      background-size: 80%; } }
  @media (max-width: 768px) {
    .page__main {
      background: none;
      margin-bottom: 30px; } }
  @media (max-width: 767px) {
    .page__main {
      margin-bottom: 20px; } }

.page__footer {
  width: 100%; }

.header {
  min-width: 320px;
  display: flex;
  align-items: center;
  padding-top: 34px;
  padding-bottom: 31px; }
  @media (max-width: 768px) {
    .header {
      flex-direction: column;
      align-items: flex-start;
      padding: 0; } }

.header__wrapper {
  width: 90%;
  display: flex;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto; }
  @media (max-width: 768px) {
    .header__wrapper {
      width: 100%; } }

.header__top {
  width: 57px;
  display: flex;
  align-items: center;
  margin-right: 30px;
  padding-bottom: 3px; }
  @media (max-width: 768px) {
    .header__top {
      width: 100%;
      min-height: 50px;
      padding-bottom: 0;
      margin-right: 0; } }

.header__container {
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  margin-right: 15px;
  z-index: 1; }
  @media (max-width: 768px) {
    .header__container {
      display: none;
      width: 100%;
      justify-content: center;
      align-items: center;
      margin-left: 0;
      margin-right: 0; } }
  @media (max-width: 768px) {
    .header__container--open {
      position: absolute;
      min-width: 320px;
      display: flex;
      background-color: #FBF8F8;
      padding-top: 50px;
      padding-bottom: 50px; } }
  @media (max-width: 768px) {
    .header__container--nojs {
      display: flex; } }

.header__logo {
  line-height: 0;
  padding-left: 375px; }
  @media (max-width: 768px) {
    .header__logo {
      margin-top: 4px;
      padding-left: 21px; } }
  @media (max-width: 767px) {
    .header__logo {
      padding-left: 16px; } }
  .header__logo svg {
    width: 57px;
    height: 22px; }
    @media (max-width: 768px) {
      .header__logo svg {
        width: 44px;
        height: 18px; } }

.nav {
  display: flex;
  flex-wrap: wrap;
  margin-right: 55px; }
  @media (max-width: 768px) {
    .nav {
      width: 100%;
      padding-top: 99px;
      padding-bottom: 99px;
      margin: 0; } }

.nav__list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center; }
  @media (max-width: 768px) {
    .nav__list {
      flex-direction: column;
      align-items: flex-start; } }

.nav__item {
  margin-right: 41px; }
  .nav__item:last-child {
    margin-right: 0; }
    @media (max-width: 768px) {
      .nav__item:last-child {
        margin-bottom: 0; } }
  @media (max-width: 768px) {
    .nav__item {
      width: 100%;
      margin-right: 0;
      margin-bottom: 30px; } }

.nav__link {
  color: #141517;
  font-size: 14px;
  line-height: 17px; }
  @media (max-width: 768px) {
    .nav__link {
      position: relative;
      width: 100%;
      display: block;
      color: #48494D;
      font-size: 20px;
      line-height: 28px;
      padding: 17px 50px 17px 35px; }
      .nav__link::after {
        position: absolute;
        top: 50%;
        right: 25px;
        transform: translateY(-50%);
        display: block;
        content: "";
        width: 10px;
        height: 13px;
        background-image: url("../img/icon-arrow-right.svg");
        background-repeat: no-repeat;
        background-position: center; } }
  @media (min-width: 1024px) {
    .nav__link:hover, .nav__link:focus {
      color: #FE7865; } }

.contact-us {
  position: relative;
  display: flex;
  align-items: center;
  padding-top: 2px; }
  @media (max-width: 768px) {
    .contact-us {
      display: none; } }
  .contact-us--prices {
    width: 100%;
    flex-direction: column;
    align-items: flex-end;
    padding-top: 0; }
    @media (max-width: 768px) {
      .contact-us--prices {
        display: flex;
        align-items: center; } }

.contact-us__icon-phone {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  fill: none;
  stroke: #141517; }
  .contact-us__icon-phone--prices {
    top: 15px;
    left: -6px;
    stroke: #F17D4B; }
    @media (max-width: 768px) {
      .contact-us__icon-phone--prices {
        display: none; } }

.contact-us__tel {
  width: 227px;
  color: #141517;
  font-size: 18px;
  line-height: 32px;
  text-decoration: underline;
  padding-left: 39px; }
  .contact-us__tel--prices {
    width: 248px;
    font-size: 20px;
    line-height: 32px;
    color: #141517;
    padding-left: 44px;
    text-decoration: none; }
    @media (max-width: 768px) {
      .contact-us__tel--prices {
        width: 188px;
        font-size: 18px;
        text-align: center;
        text-decoration: underline;
        color: #FE7865;
        padding-left: 0;
        margin-right: 0; } }
  .contact-us__tel:hover, .contact-us__tel:focus {
    color: #FE7865; }
  .contact-us__tel:hover + .contact-us__icon-phone,
  .contact-us__tel:focus + .contact-us__icon-phone {
    stroke: #FE7865; }

.button {
  display: block;
  width: 288px;
  height: 75px;
  padding-top: 30px;
  padding-bottom: 30px;
  padding-right: 30px;
  padding-left: 40px;
  color: #ffffff;
  background-color: #FE7865;
  font-size: 12px;
  font-weight: 700;
  line-height: 14.63px;
  letter-spacing: 2px;
  text-transform: uppercase;
  border: 0;
  cursor: pointer; }
  @media (max-width: 768px) {
    .button {
      width: 250px; } }
  @media (max-width: 767px) {
    .button {
      height: 50px;
      padding-top: 20px;
      padding-bottom: 20px;
      font-weight: 500;
      font-size: 10px;
      line-height: 12px; } }
  .button:hover, .button:focus {
    background-color: #F17D4B; }

.button-burger {
  display: none;
  position: relative;
  outline: none;
  border: 0; }
  @media (max-width: 768px) {
    .button-burger {
      display: block;
      width: 72px;
      height: 50px;
      background-color: #FE7865;
      background-image: url("../img/icon-menu.svg");
      background-repeat: no-repeat;
      background-position: center;
      margin-left: auto; } }
  @media (max-width: 767px) {
    .button-burger {
      width: 50px; } }

.button-cross {
  display: none;
  position: relative;
  outline: none;
  border: 0; }
  @media (max-width: 768px) {
    .button-cross {
      display: block;
      width: 72px;
      height: 50px;
      position: absolute;
      top: 0;
      right: 0;
      background-image: url("../img/icon-close.svg");
      background-repeat: no-repeat;
      background-position: center; } }
  @media (max-width: 767px) {
    .button-cross {
      width: 50px; } }

.header__container--nojs > .button-cross {
  display: none; }

.button--places {
  width: 266px;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.7);
  padding: 30px 23px;
  margin-left: auto;
  margin-right: auto; }
  @media (max-width: 768px) {
    .button--places {
      display: none; } }
  .button--places:hover, .button--places:focus, .button--places:active {
    background-color: transparent; }

